import React, { Component } from "react";
import * as THREE from "three";



class App extends Component {
  componentDidMount() {
    // === THREE.JS CODE START ===


   



    function scene() {    
      scene = new THREE.Scene();
      scene.background = new THREE.Color( 0x121213 );  
      
    }


    function AddMesh() {

      var geometry = new THREE.BoxGeometry( 2, 2, 2 );
      var material = new THREE.MeshStandardMaterial( { color: 0xB0C4DE } );
      var cube = new THREE.Mesh( geometry, material );
  
      return cube
    }



    function Heart() {
      var x = 0, y = 0;

      var heartShape = new THREE.Shape();
      
      heartShape.moveTo( x + 5, y + 5 );
      heartShape.bezierCurveTo( x + 5, y + 5, x + 4, y, x, y );
      heartShape.bezierCurveTo( x - 6, y, x - 6, y + 7,x - 6, y + 7 );
      heartShape.bezierCurveTo( x - 6, y + 11, x - 3, y + 15.4, x + 5, y + 19 );
      heartShape.bezierCurveTo( x + 12, y + 15.4, x + 16, y + 11, x + 16, y + 7 );
      heartShape.bezierCurveTo( x + 16, y + 7, x + 16, y, x + 10, y );
      heartShape.bezierCurveTo( x + 7, y, x + 5, y + 5, x + 5, y + 5 );
      
      var geometry = new THREE.ShapeGeometry( heartShape );
      var material = new THREE.MeshStandardMaterial( { color: 0x0078FF } );
      var heart = new THREE.Mesh( geometry, material ) ;
    
      return heart
    }


    function SetCamera(x,y,z) {


      camera = new THREE.PerspectiveCamera( 
        75, 
        window.innerWidth/window.innerHeight, 
        0.1, 
        1000 );

       camera.position.set( x,y,z);

        return camera
    }



    function animate () {
      requestAnimationFrame( animate );
      object.rotation.x += 0.006;
      object.rotation.y += 0.006;
      object.rotation.z += 0.006;

      renderer.render( scene, camera );
    };
  

    function renderIt () {
      var renderer = new THREE.WebGLRenderer({ antialias: true });

      renderer.setSize( window.innerWidth, window.innerHeight );

      
      renderer.setPixelRatio( window.devicePixelRatio );
      
      renderer.gammaFactor = 2.2;
      renderer.gammaOutput = true;

      renderer.physicallyCorrectLights = true;

      document.body.appendChild( renderer.domElement );

      return renderer
    }


    function onWindowResize() {
      // set the aspect ratio to match the new browser window aspect ratio
      camera.aspect = window.innerWidth / window.innerHeight;
    
      // update the camera's frustum
      camera.updateProjectionMatrix();
    
      // update the size of the renderer AND the canvas
      renderer.setSize( window.innerWidth, window.innerHeight );
      }
      window.addEventListener( 'resize', onWindowResize );



    function createLights() {
      
    
      const ambientLight = new THREE.HemisphereLight(
          0xddeeff, // bright sky color
          0x202020, // dim ground color
          3, // intensity
        );
    
      const mainLight = new THREE.DirectionalLight( 0xffffff, 2 );
      mainLight.position.set( 10, 10, 10 );
    
      scene.add( ambientLight, mainLight );
  
    }

    


    

    scene();
    var camera = SetCamera(0,0,45)
    var cube = AddMesh();
    // scene.add( cube );


    var heart = Heart()
    scene.add( heart );

    createLights()

    var renderer = renderIt ()

    var object = heart;
    animate();
    // === THREE.JS EXAMPLE CODE END ===
  }

  render() {
    return (
      <div />

    )
  }
}


export default App